import Link from "next/link";
import { Rss, Tag } from "lucide-react";

// üí° 1. rss2json APIÎ•º ÏÇ¨Ïö©ÌïòÎèÑÎ°ù URL Î≥ÄÍ≤Ω
//    - api_keyÎäî Î¨¥Î£å ÌîåÎûúÏóêÏÑú ÌïÑÏöî ÏóÜÏäµÎãàÎã§.
const RSS_TO_JSON_API = "https://api.rss2json.com/v1/api.json?rss_url=";
const GOOGLE_KOREA_RSS_URL = "https://blog.google/intl/ko-kr/feed/";

// üí° 2. rss-parserÏóêÏÑú Ï†ïÏùòÌïú ÌÉÄÏûÖÏùÑ rss2json ÏùëÎãµ ÌÉÄÏûÖÏúºÎ°ú Î≥ÄÍ≤Ω
type FeedItem = {
  title: string;
  link: string;
  pubDate: string; // rss2jsonÏùÄ ÎÇ†Ïßú ÌòïÏãùÏù¥ Îã§Î•º Ïàò ÏûàÏùå
  description: string; // contentSnippet ÎåÄÏã† description ÏÇ¨Ïö©
  categories: string[];
};

type RssApiResponse = {
  status: string;
  items: FeedItem[];
};

/**
 * RSS ÌîºÎìúÎ•º Í∞ÄÏ†∏Ïò§Í≥† 'AI' ÌÇ§ÏõåÎìúÎ°ú ÌïÑÌÑ∞ÎßÅÌïòÎäî Ìï®Ïàò
 */
async function getAiFeed() {
  try {
    // üí° 3. rss2json APIÎ°ú JSONÏùÑ ÏßÅÏ†ë fetch
    const response = await fetch(
      `${RSS_TO_JSON_API}${encodeURIComponent(GOOGLE_KOREA_RSS_URL)}`,
      {
        next: { revalidate: 3600 }, // 1ÏãúÍ∞Ñ Ï∫êÏãú
      }
    );

    if (!response.ok) {
      throw new Error(`Failed to fetch RSS feed: ${response.statusText}`);
    }

    const data: RssApiResponse = await response.json();

    if (data.status !== "ok") {
      throw new Error("rss2json API failed to parse feed");
    }

    // üí° 4. ÌååÏã± ÎùºÏù¥Î∏åÎü¨Î¶¨(rss-parser) ÏóÜÏù¥ Î∞îÎ°ú JSONÏùÑ ÌïÑÌÑ∞ÎßÅ
    const keyword = "ai"; // ÎåÄÏÜåÎ¨∏Ïûê Íµ¨Î∂Ñ ÏóÜÏù¥ 'ai'

    const filteredItems = data.items.filter((item) => {
      // üí° 5. rss2json ÌïÑÎìúÎ™Ö(description)ÏúºÎ°ú Î≥ÄÍ≤Ω
      const titleMatch =
        item.title?.toLowerCase().includes(keyword) ?? false;
      const descriptionMatch =
        item.description?.toLowerCase().includes(keyword) ?? false;
      const categoryMatch =
        item.categories?.some((cat) => cat.toLowerCase().includes(keyword)) ??
        false;

      return titleMatch || descriptionMatch || categoryMatch;
    });

    return filteredItems;
  } catch (error) {
    console.error("Error fetching or parsing Google Korea RSS feed:", error);
    return [];
  }
}

/**
 * Google Korea AI ÌîºÎìúÎ•º Î†åÎçîÎßÅÌïòÎäî ÏÑúÎ≤Ñ Ïª¥Ìè¨ÎÑåÌä∏
 */
export default async function GoogleKoreaAiFeed() {
  const items = await getAiFeed();

  if (items.length === 0) {
    return (
      <p className="text-gray-500">
        Google Korea Î∏îÎ°úÍ∑∏ÏóêÏÑú AI Í¥ÄÎ†® ÏÉà ÏÜåÏãùÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.
      </p>
    );
  }

  return (
    <div className="border border-gray-800 rounded-lg p-6 bg-gray-900/30">
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-2xl font-light">Google Korea AI ÏÜåÏãù</h2>
        <a
          href="https://rss2json.com/"
          target="_blank"
          rel="noopener noreferrer"
          title="Powered by rss2json"
        >
          <Rss size={18} className="text-gray-500 hover:text-white" />
        </a>
      </div>
      <ul className="space-y-4">
        {items.slice(0, 5).map((item) => (
          <li key={item.link} className="border-b border-gray-800 pb-3 last:border-b-0">
            <Link
              href={item.link}
              target="_blank"
              rel="noopener noreferrer"
              className="text-gray-300 hover:text-white transition-colors"
            >
              {item.title}
            </Link>
            {/* Ïπ¥ÌÖåÍ≥†Î¶¨(ÌÉúÍ∑∏) ÌëúÏãú */}
            <div className="flex items-center gap-2 mt-2">
              <Tag size={14} className="text-gray-600" />
              <div className="flex gap-2 flex-wrap">
                {item.categories?.map((tag) => (
                  <span
                    key={tag}
                    className="text-xs px-2 py-1 rounded bg-gray-800 text-gray-400"
                  >
                    {tag}
                  </span>
                ))}
              </div>
            </div>
            {item.pubDate && (
              <p className="text-xs text-gray-500 mt-2">
                {new Date(item.pubDate).toLocaleDateString()}
              </p>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
}